import React from "react"
import { Helmet } from "react-helmet";

export default class HomeAppliances extends React.Component {
    render() {
        return (
            <div className='homepage center center-page-x' style={{paddingBottom: '50px'}}>
                <Helmet>
                    <meta charSet="utf-8" />
                    <title>Interactive 3d product visualization and AR for the home appliance industry</title>
                    <link rel="canonical" href="https://www.ezyvue.com/industries/homeAppliances" />
                </Helmet>
                <section className="full-width-viewer-header">
                    <div>
                            <h1>Realistic 3D visualization for electrical appliance retailers and manufacturers</h1>
                            <h3>Take your product visuals to a new dimension with the 3D Viewer</h3>
                    </div>
                    <div>
                        <div className='homepage-viewer viewer-outer'>
                            <div className='aspect-inner'>
                                <iframe id="ezyvue-viewer"
                                    src={`${process.env.REACT_APP_VIEWER_URL}/viewer?productId=63c396a24560352a23948847&hidemenu`}
                                    width="100%"
                                    height="100%"
                                    style={{ border: 'none' }}
                                    frameBorder="0"
                                    allow="xr-spatial-tracking"
                                    allowFullScreen={true}
                                    mozallowfullscreen="true"
                                    webkitallowfullscreen="true" />
                            </div>
                        </div>
                    </div>    
                </section>
                <section className='homepage-section'>
                    <div className="flexrow">
                        <div className='flexcol'>
                            <h2>Highlight product features</h2>
                            3d annotations allow you to demonstrate features of an appliance and provide product information in an interactive and visual way.<br />
                            Shoppers can quickly zoom in and get information about key functionality at the click of a button.
                        </div>
                        <div className='flexcol'>
                            <img className='homepage-section-img' alt='3d annotations'
                                src={require('../../assets/images/toaster-detail.jpg')} loading='lazy' />
                        </div>
                    </div>
                </section>
                <section className='homepage-section'>
                    <div className="flexrow">
                    <div className="flexcol">
                            <h2>Let customers personalize a product</h2>
                            <p>
                                With the Product Configurator customers can choose from all the product options.<br/>
                                Customers are more likely to continue through to the checkout if they can personalize a product.
                            </p>
                        </div>
                        <div className='flexcol'>
                            <img style={{ opacity: '90%', width: '400px' }} className='homepage-section-img' alt='3d animations'
                                src={require('../../assets/images/toaster-variants.jpg')} loading='lazy' />
                        </div>
                    </div>
                </section>
                <section className='homepage-section'>
                    <div className="flexrow">
                        <div className='flexcol'>
                            <h2>Showcase working appliances with animations</h2>
                            Make the shopping experience more interactive and demonstrate features, mechanisms and moving parts with animations and animation triggers. <br />
                        </div>
                        <div className='flexcol'>
                            <img style={{ opacity: '90%', width: '400px' }} className='homepage-section-img' alt='3d animations'
                                src={require('../../assets/images/animation.gif')} loading='lazy' />
                        </div>
                    </div>
                </section>
                <section className='homepage-section'>
                    <div className="flexrow">
                        <div className='flexcol'>
                            <h2>Consumers can try out appliances in the home with AR</h2>
                            <p>
                                In consumer studies AR has shown to improve customer engagement and brand awareness, increase sales and reduce returns.<br/>
                                A customer can switch to AR from the viewer without installing a mobile app and with WebAR they can even view all the available product options.
                            </p>
                        </div>
                        <div className='flexcol'>
                            <div className="smartphone" style={{ width: '255px' }}>
                                <div className="content">
                                    <img alt='augmented reality for ios and android' src={require('../../assets/images/ar1.gif')} />
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}